.logo-container{
    width:100%;
    position: sticky;
    top: -200px;
    z-index: 2;
    background: whitesmoke;
    text-shadow: 6px 2px lightgrey;
}
#website-logo{
    color: #1368CE;
    font-size: 75px;
    text-align: center;
    height: 150px;
    padding-top:20px;
    font-family: sans-serif;
}

@media (max-width: 420px) {
    #website-logo{
        font-size: 15vw !important;
        padding-top: 50px;
    }
}




