.scoreBarOuterSection{
    position:relative;
    width:100%;
    height:30px;
    background-color: lightgray;
    box-shadow: 5px 5px 12px -2px grey inset;
    border-radius: 4px;
    
}

.scoreBarInnerSection{
   position: absolute;
   height:100%;
   box-shadow: 2px 2px 4px -1px grey inset;
   border-radius: 4px;
}

.scoreBarRank{
    position:absolute;
    border: 4px solid white;
    border-radius: 50%;
    width: 45px;
    height: 45px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 1.2rem;
    font-weight: bold;
    color:white;
    box-shadow: 4px 4px 12px -2px grey;
}