button{
  cursor: pointer;
}

button[disabled]{
  cursor:default;
}


::-webkit-scrollbar {
    width: 10px;
    position: absolute;
    right:0;
}
  
  /* Track */
::-webkit-scrollbar-track {
    background: #f1f1f1;
}
   
  /* Handle */
::-webkit-scrollbar-thumb {
    background: #888;
    border-radius: 8px;
}
  
  /* Handle on hover */
::-webkit-scrollbar-thumb:hover {
    background: #555; 
}


body{
    margin: 0;
    background: whitesmoke;
    overflow-y: scroll;
    overflow-x: hidden;
}

.unselectable,i {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}

.toggle-volume i {
  font-size: 2rem;
}

.menu-btn, .exit{
  display: none;
}

.menu-btn i, .toggle-volume i{
  font-size: 2rem;
}
.exit i{
  font-size: 1.7rem;
}

.menu-btn i:hover, .exit i:hover , .toggle-volume i:hover{
  text-shadow: 1px 1px grey;
  cursor: pointer;
}

