.quiz-transition-container{
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    background: #24282c;

}

.playername-container{
    width: 100%;
    margin: 20px;
    display: flex;
    justify-content:center;
}
.playername{
    display: flex;
    justify-content:center;
    align-items:center;
    color: white;
    width: 75%;
    /* font-weight: 300; */
    font-size: 8vmin;
    text-shadow: 2px 2px black;
    border-bottom : 1px solid white;
    font-family: 'Montserrat';
}

.curr-score-container{
    margin-top: 50px;
    height: 200px;
    display: flex;
    flex-direction: column;
    align-items:center;
}

.curr-score-title,.curr-score{
    font-size: 8.2vmin;
    font-weight: 500;
    color: white;

}
.curr-score-title{
    text-decoration: underline;
    font-family: 'Roboto Condensed';
    
}

.progressbar-container{
    width: 80%;
    position: absolute;
    bottom: 50px;
}
.progress{
    border-radius: 3px !important;
    height: 25px;
    background: transparent !important;
}
.progress-bar{
    background: white
}