.info-icon{
    cursor: pointer;
    
}
.info-icon-container{
    transition: transform .2s ease-in-out;
}
.info-icon-container:hover{
    transform: scale(1.05);
}
.info-anim{
    animation: info-animate 500ms ease-in-out infinite alternate;
}
@keyframes info-animate {
    0%{
        box-shadow: 0px 0px 6px 3px #0f4f9c;
    }
    100%{
        box-shadow: unset;
    }
}

.contact-card-outer{
    width:200px;
    height:150px;
    display:flex;
    flex-direction: column;
    perspective: 1000px;
    box-shadow: 3px 3px 10px -3px rgb(172, 172, 172);
    background-color:transparent;
}

