
.quiz-card{
    box-shadow: 6px 9px 12px -5px lightgrey;
    min-width: 25%;
    border-radius: 0px;
    padding: 2%;
    display: flex;
    flex-direction: row;
    top: 0px;
    transition: transform 0.2s ease-in-out;
}
.card-title{
    font-family: 'Martel';
    font-weight: 600;
}

.quiz-card:hover{
    transform: scale(1.015);
}

.card-text{
    font-size: 0.9rem;
    font-family: 'Montserrat'
}
.btn-css{
    background-color: #1368CE;
    color: white;
    box-shadow: 3px 6px 9px -2px lightgrey;
    font-family: 'Montserrat';
}
.btn-css:hover{
    color: white;
    background-color: #125ebb;
}

.time{
    position: absolute;
    font-size: 1rem;
    right: 40px;
    font-family: 'Montserrat';
    font-weight: bold;
}



@media (max-width: 600px){
    .quiz-card{
        flex-direction: row;
    }
    .card-img{
        width:35% !important;
    }
}

@media (max-width: 992px){
    .quiz-card{
        flex-direction: column;
    }
    .time{
        display: none;
    }
    .card-desc{
        display: none;
    }
    .card-img{
        width:100% !important;
    }
}