
  #GreetOuter {
    width: 100vw;
    height: 100vh;
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    Z-index:3;
    background-color: whitesmoke;
  }
  
  #GreetDiv1 { 
    position: absolute;
  }
  
  #GreetHead1 {
    font-size: 0;
    font-family: sans-serif;
    font-weight: 100;
    color: #1368ce;
  }

  #GreetHead2{
    font-size: 10vw;
    font-family: sans-serif;
    font-weight: 100;
    color: #1368ce;
  }

  #GreetDiv2{
    position: relative;
    left:10vw;
    top:10vh;
    display:none;
    align-self: center;
  }

@media (max-width: 1024px) {
  #GreetDiv2{
    top: 3.5vh;
  }
}
@media (max-width: 480px) {
  #GreetDiv2{
    top: 2.8vh;
  }
}
  