.switch-btn-container{
    position: sticky;
    top: -5px;
    width: 100%;
    z-index: 2;
    background: whitesmoke;
    box-shadow: 0 6px 12px -3px lightgrey;
    
}

.switch{
    width: 50%;
    border-radius: 0px !important;
    font-family: 'Merriweather';
}
.switch:hover{
    background: rgb(241, 241, 241);
}
.btn-selected{
    background: #1368CE;
    color: white;
    border: none;

}
.btn-selected:hover{
    background: #125ebb;
}
.switch:hover{
    border: 1px solid lightgrey;
}

#mainpage-heading{
    position: sticky;
    height: 120px;
    padding: 50px;
    background: whitesmoke;
    top: 30px;
    font-family: 'Martel',sans-serif;
    text-align: center;
    border-bottom: 1px solid lightgrey;
    margin-bottom: 10px;
    padding-bottom: 15px;
    box-shadow: 0 12px 12px -18px #1268cd;
    z-index: 1;
}


.search-container{
    position: sticky;
    margin-left: 10px;
    top: 100px;
    z-index: 3;
}
.search-container i{
    font-size: 1.8rem;
    position: relative;
    color: #353a40;
    height: 45px;
    width: 45px;
    padding: 5px;

}
.search-container input{
    font-family: 'Martel';
    border-top-left-radius: 0;
    border-bottom-left-radius: 0;
}
.search-container input:focus{
    box-shadow: none;
}

@media (max-width: 540px){
    .search-container{
        position: relative;
        top: 0;
        z-index: 0;
    }
}

@media (max-width: 420px){
    #mainpage-heading{
        font-size: 1.8rem;
    }
    table * {
        font-size: 0.8rem;
    }
    .search-container{
        position: relative;
        top: 0;
        z-index: 0;
    }
    .search-container input{
        width: 75%;
    }
}

@media (max-width: 320px){
    .search-container{
        margin-left: 0;
    }
    .search-container i{
        margin-right: 1px;
    }
}
@media (max-width: 280px){
    .search-container{
        margin-left: 0;
    }
    .search-container i{
        margin: 0;
    }
    .search-container input{
        width: 75%;
    }


}
