
.quiz-app-container{
    overflow-x: hidden;
    overflow-y: hidden;
}

.question-container{
    width: 100%;
    min-height: 8vh;
    display: flex;
    padding:  30px 20px;
    background: white;
    align-items: center;
    box-shadow: 3px 0px 6px 1px grey;
    z-index: 2;
}
.que-number{
    margin-left: 20px;
    min-width: 60px;
    min-height: 60px;
    font-size: 2rem;
    font-weight: bold;
    border-radius: 50%;
    background: #1268cd;
    color: white;
    display :flex;
    justify-content:  center;
    align-items: center;
}

.question-container .que{
    width: 100%;
    text-align: center;
    line-height: 1.25;
    font-weight: 600;
    letter-spacing: -0.063rem;
    white-space: pre-wrap;
    font-size: 4.5vmin;
    font-family: 'Merriweather', serif;
}


.middle-container{
    width: 100%;
    min-height: 250px;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
}

.timer-container{
    position: absolute;;
    right: 30px;

}

.base-timer {
    position: relative;
    width: 75px;
    height: 75px;
}
.base-timer__svg {
    transform: scaleX(-1);
}
.base-timer__circle {
    fill: none;
    stroke: none;
}
.base-timer__path-elapsed {
    stroke-width: 7px;
    stroke: grey;
}
.base-timer__path-remaining {
    stroke-width: 7px;
    stroke-linecap: round;
    transform: rotate(90deg);
    transform-origin: center;
    transition: 1s linear all;
    fill-rule: nonzero;
    stroke: currentColor;
}
.base-timer__path-remaining.green {
    color: rgb(65, 184, 131);
}
.base-timer__path-remaining.orange {
    color: orange;
}
.base-timer__path-remaining.red {
    color: red;
}
.base-timer__label {
    position: absolute;
    width: 75px;
    height: 75px;
    top: 0;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 25px;
}

.notification{
    position: absolute;
    top: 0;
    width: 100%;
    height: 70px;
    border-radius: 3px;
    justify-content: center;
    align-items: center;
    font-size: 5.5vmin;
    font-family: 'Ubuntu', sans-serif;
}


.que-img{
    position: relative;
    z-index: -1;
    background-position: center center;
    background-repeat: no-repeat;
    background-size: contain;
    width: 100%;
    height: 50vh/*350px*/
}

.default{
    display: none;
}
.correct{
    display: flex;
    background: #29c329;
    color: white;
}
.wrong{
    display: flex;
    background: #f82b2b;
    color: white;
}

.timeup{
    display: flex;
    background: darkorange;
    color: white;
}


.next-btn, .finish-btn{
    width: 75px;
    height: 45px;
    color: white;
    outline: none;
    border: none;
    border-radius: 4px;
    box-shadow: 2px 2px 6px -1px grey;
    font-size: 1.5rem;
    font-family: 'Montserrat';
    transition: all .2s ease-in-out;
    box-sizing:content-box;
    margin-right: 20px;
    position: absolute;
    right: 0;
    top: 150px;
}
.next-btn{
    background:#1268cd;
}
.next-btn:focus{
    outline: none;
    border: none;
}
.next-btn:hover{
    background: #115eb6;
}

.finish-btn{
    background:rgb(65, 184, 131);
}
.finish-btn:hover{
    background:rgb(48, 167, 113);
}


.option-container{
    position: absolute;
    width: 100%;
    bottom: 20px;
    display: flex;
    flex: 4 1 0%;
    flex-wrap: wrap;
    align-content: stretch;
    overflow-x: hidden;
    overflow-y: hidden;

}
.option{
    position: relative;
    overflow: visible;
    align-items: center;
    justify-content:center;
    width: calc(50% - 3rem);
    padding: 1rem;
    border-radius: 4px;
    min-height: 12.5vh;
    max-width: 100%;
    display: flex;
    flex: 1 0 auto;
    margin: 0.5rem;
    cursor: pointer;
    font-size: 3vmin;
    font-weight: 500;
    background:  white;
    box-shadow: 4px 4px 6px -1px lightgrey;
    color: rgba(0, 0, 0, 0.7);
    border: 2px solid lightgrey;
    transition: all 0.2s ease-in-out;
    font-family: 'Merriweather'
}


.option:focus {
    outline: none;
}

.option[data="false"]:hover{
    background:rgb(248, 248, 248);
    transform: scale(1.01);
    border: 1px solid #1269cd;
}

.option[disabled]{
    color: rgb(167, 167, 167);
}



.correctAnswer{
    background: #29c329;
    color: white;
}
.correctAnswer[disabled]{
    color: white
}

.correctAnswer i::after{
    content: "check";
}

.wrongAnswer{
    background: #f51e1e;
    color: white;
}
.wrongAnswer[disabled]{
    color: white;
}

.wrongAnswer i::after{
    content: "clear";
}

@media (max-width: 876px){
    .option{
        font-size: 2.6vmin;
    }
    
}

@media (max-width: 560px){
    .option{
        font-size: 2.5vmin;
        min-height: 4.25rem;
    }
    .que-number{
        top: 76vh;
        z-index: 0;
        min-width: 35px;
        min-height: 35px;
        font-size: 1rem;
    }
    .notification{
        height: 50px;
    }
    .option-container{
        bottom: 12vh;
    }
    .middle-container{
        position: absolute;
    }
    .timer-container{
        top: 75vh;
    }
    .base-timer{
        width: 38px;
        width: 38px;
    }
    .base-timer__label{
        width: 38px;
        height: 38px;
        font-size: 14px;
    }
    .que-img{
        position: absolute;
        /* margin-top: 0; */
        top: -50px;
    }
    .next-btn, .finish-btn{
        top: 76vh;
        width: 60px;
        height: 30px;
        font-size: 1.1rem;
    }
    
}