.leaderboard-combobox{
    height: 45px;
    width: min-content;
    font-size: 1.2rem;
    margin-bottom: 10px;
    font-family: 'Montserrat';
}

table th{
    font-family: 'Montserrat' !important;
    font-size: 1.2rem;
    word-spacing: 5px;
}
table td{
    font-family: 'Martel';
}
.contender-details{
    height: 100%;
    display: none;
    align-items: center;
    padding-left: 10px;
    color: white;
    font-size: 1.3rem;
    font-weight: bolder;
    letter-spacing: 1.3px;
    text-shadow: 1px 1px grey;
}
.contender-details .cont-score{
    position: absolute;
    right: 50px;
}


@media (max-width: 480px) {
    .hide-data{
        display: none;
        position: absolute;
    }
    table,table th, table td{
        width: 100%;
    }
    .contender-details{
        display: flex;
    }
}