.contact-card-inner{
    background-color:white;
    border:1px solid lightgrey;
    height:100%;
    width:100%;
    transition: transform 0.6s;
    transform-style: preserve-3d;
    display:flex;  
    justify-content: space-evenly; 
    align-items: center;
    border-radius: 4px;
    font-family: 'Montserrat';
}
.contact-card-front {
    display: flex;
    padding-left: 10px ;
    align-items: center;
}

.contact-card-front span{
    margin-left:10px;
}

.contact-card-front, .contact-card-back{
    position: absolute;
    width: 100%;
    -webkit-backface-visibility: hidden;
    backface-visibility: hidden;
    
}

.contact-card-inner:hover {
    transform: rotateX(180deg);
}

.contact-card-back {
    transform: rotateX(180deg);
    display: flex;
    justify-content: space-evenly;
    align-items:center;
    
}

.contact-card-back div:hover{
    transform:scale(1.1);
}