.quiz-end-container{
    width: 100%;
    height: 100vh;
    overflow: hidden;
    background: #061e39;
    color: white;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    z-index: -3;
    position: absolute;
}

.final-score-container , .highscore-container, .end-btns-container{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    margin-top: 50px;
}


.final-score-title{
    font-size: 14vmin;
    border-bottom: 2px solid white;
    text-shadow: 4px 4px black;
    font-family: 'Roboto Condensed';
    box-shadow: 0 10px 5px -5px black;
}
.final-score{
    font-size: 7vmin;
    text-shadow: 2px 2px black;
    
}
.current-username,.highscorer{
    font-size: 1.2rem; 
    text-shadow: 2px 2px black;
    font-family: 'Montserrat';
}
.current-username{
    color: dodgerblue;
}

.highscore-title{
    font-size: 6vmin;
    border-bottom: 1px solid white;
    text-shadow: 4px 4px black;
    font-family: 'Roboto Condensed';
    box-shadow: 0 10px 5px -5px black;
}

.new-highscore-title{
    color: gold;
}

.highscore{
    font-size: 5vmin;
    text-shadow: 2px 2px black;
}
.highscorer{
    color: orangered;
}
.end-btns-container{
    display: flex;
    flex-direction: row;
}
.home-btn, .playagain-btn{
    background: transparent;
    border: none;
    color: white;
    text-shadow: 2px 2px black;
    
}
.home-btn:focus, .playagain-btn:focus{
    outline: none;
}

.home-btn i, .playagain-btn i{
    transition: all .2s ease-in-out;
}

.home-btn i:hover, .playagain-btn i:hover{
    color: dodgerblue;
    transform: scale(1.15);
    text-shadow: 4px 4px black;
}


.party-poppers{
    position: fixed;
    bottom: -51px ;
    height: 40vw ;
    width: 40vw ;
    background-size:cover ;
    background-position:center
}

.party-poppers-mobile{
    width: 100%;
    height: 100vh;
    color: white;
    position: absolute;
    display: none;
    z-index: -1;
}

@media (max-width: 768px ){
    .party-poppers{
        display: none !important;
    }
    .party-poppers-mobile{
        display: inherit;
    }
}


