.entry-form{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    align-items: center;
    height: 100vh;
    /* background: #061e39; */
    background-position: center;
    background-size: cover;

}

.entryform-logo{
    color: red;
    font-size: 100px;
    text-align: center;
    width: 100%;
    height: 100px;
    margin: 80px;
    font-weight: 500;
    text-shadow: 6px 2px black;
    font-family: 'Ubuntu';
}

.entry-inputs{
    width: 620px;
    height: 300px;
    background: whitesmoke;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 0.25rem;
    box-shadow: 0px 3px 6px 2px black;
    font-family: 'Ubuntu';
    margin-bottom: 50px;
}

.entry-form input{
    width: 550px;
    height: 50px;
    font-size: 1.5rem;
    padding: 24px;
    text-align: center;
    color: #495057;
    background-color: #fff;
    background-clip: padding-box;
    border: 1px solid #ced4da;
    border-radius: .25rem;
    transition: background-color,border-color .15s ease-in-out;
}


.entry-form button{
    width: 550px;
    margin-top: 20px;
    height: 50px;
    font-size: 1.5rem;
    font-weight: 400;
    text-align: center;
    border: 1px solid transparent;
    padding: .375rem .75rem;
    line-height: 1.5;
    border-radius: .25rem;
    transition: color .15s ease-in-out,background-color .15s ease-in-out,border-color .15s ease-in-out,box-shadow .15s ease-in-out;
    color: #fff;
    background-color: #343a40;
    border-color: #343a40;
    box-shadow: 3px 3px 6px 2px rgb(187, 187, 187);
    position: relative;
}

.entry-form button:hover{
    background-color: #24282c;
    cursor: pointer;
}
.entry-form button:focus{
    outline: none;
}


.loading-container{
    height: 100vh;
    background:black;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
}
.loading-container video {
    width: 125vw;
    height: 125vh;
}

.loading-container-default{
    height: 100vh;
    background:#24282c;
    color: white;
    display: flex;
    align-items: center;
    justify-content: center;
}
.loading-display{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    text-shadow: 2px 2px black;
}

.loading-name h1{
    font-size: 100px;
    font-weight: 300;
    color: #1368CE;
    text-shadow: 4px 4px black;
    font-family: 'Ubuntu';
}

.loading-counter h3{
    font-size: 150px
}

.loading-text{
    font-size: 30px;
    font-family: monospace;
    text-decoration:wavy;
}
.loading-container-default .ready{
    color: orangered;
}
.loading-container-default .set{
    color: yellow;
}
.loading-container-default .go{
    color: lightgreen;
}


@media (max-width: 650px){
    .entryform-logo{
        font-size: 3rem;
        margin: 20px;
    }
    .entry-inputs{
        width: 255px;
        height: 260px;
    }
    .entry-form input{
        width: 200px;
        font-size: 1rem;
    }
    .entry-form button{
        width: 200px;
        font-size: 1rem;
        margin-top: 12px;
    }

    .loading-name h1{
        font-size: 35px;
    }
    
    
}
